*{
    margin: 0;
    padding: 0;
}
body {
    font-family: 'Roboto', sans-serif;
}
form {
    display: flex;
    width: 800px;
    height: auto;
    margin: 20px;
    flex-direction: column;
    justify-content: center;
}
#form-top-row {
    flex-basis: 40%;
    display: flex;
}

#form-bottom-row {
    flex-basis: 60%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
textarea {
    width:auto;
}
label {
    font-size: 0.9rem;
}
input {
    font-size: 1rem;
    border: 1px solid gray;
    padding: 5px;
    margin: 10px;
}
button {
    margin-top: 20px;
    background-color: black;
    color: white;
    border: none;
    border-radius: 5px;
    width: 100px;
    height: 50px;
    font-size: 1rem;
}
button:hover {
    opacity: 0.5;
}
